.backdrop {
    position: fixed;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1), visibility 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.modal {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    transition: 500ms linear;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    margin-top: 24px;
    width: 100vw;
    max-height: 95%;
    background: var(--additional-color);
    border-radius: 12px;
    font-size: 12px;
    overflow: auto; 
    @media screen and (min-width: 480px) {
        width: 450px;
        font-size: 14px;
    }
    @media screen and (min-width: 768px) {
        width: 480px;
            font-size: 14px;
    }
    @media screen and (min-width: 1200px) {
        width: 500px;
    }
}

.modalClose {
    position: absolute;
    right: 8px;
    top: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 5px;
    border-radius: 50px;
    &:hover, &:focus {
        background-color: var(--white-color);
    }
}

.iconClose {
    fill: var(--text-color);
}

.isHidden {
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
}

.backdrop.isHidden .modal {
    transform: translate(-50%, -50%);
}

.marquee {
    overflow: hidden;
    position: absolute;
    z-index: 20;
    color: var(--white-color);
    background-color: var(--main-color);

}

.attentionModal {
    white-space: nowrap;
    margin: 10px 0;
    display: inline-block;
    width: 100%;
    animation-name: marquee;
    animation-duration: 20s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
    animation-direction: normal;
}

@keyframes marquee {
    0% {
        transform: translateX(100%);
    }
    100% {
        transform: translateX(-100%);
    }
}

.form {
    width: 100%;
}

.textareaField {
    box-sizing: border-box;
    min-height: 30px;
    padding: 6px 12px;
    border-radius: 4px;
    resize: none;
    border: none;
    outline: transparent;
    &::placeholder{
        color: rgba(117, 117, 117, 0.5);
    }
}

.formLabel {
    position: relative;
    display: flex;
    flex-direction: column;
    gap: 4px;
    margin-bottom: 10px;
}

.inputField {
    height: 30px;
    border-radius: 4px;
    border: none;
    padding: 0px 12px;
}

.phone {
position: relative;
width: 100%;
}

.phoneCode {
    position: absolute;
    top: 8px;
    left: 6px;
    height: 30px;
    width: 60px;
}

.inputFieldPhone {
    padding-left: 55px;
    width: 100%;
}

.lableWrap {
    display: flex;
    gap: 10px;
    margin: 10px 0px;
}
.kindLable {
    width: 50px;
    height: 50px;
    background-color: var(--white-color);
    border-radius: 14px;
}
.materialLable {
    width: 30px;
    height: 30px;
    border-radius: 14px;
}
.radioBtn {
    position: absolute;
    opacity: 0;
    width: 0;
    height: 0;
}
.radioImg {
    cursor: pointer;
    width: 100%;
    height: 100%;
    border-radius: 14px;
    object-fit: contain;
}
.radioBtn:checked + .radioImg {
    outline: 3px solid var(--main-color);
    border-radius: 14px;
    outline-offset: 3px;
}

.orderBtnWrap {
display: flex;
flex-direction: column;
align-items: center;
}

.orderBtn {
box-shadow: var(--shadow);
margin-top: 10px;
&:hover, &:focus {
    background-color: var(--white-color);
}
}